import React from "react";
import Cloud from "./Cloud.jsx";
import Hill from "./Hill.jsx";
import Building from "./Building.jsx";
import BuildingBkg from "./Building_bkg.jsx";
import LogoGroup from "./logo.js";
import Title from "./Title.jsx";
import SurveyGrid from "./SurveyGrid.jsx";
import { Parallax, ParallaxLayer } from "@react-spring/parallax";
import { makeStyles } from "@material-ui/core/styles";

import { ReactComponent as Standing19 } from "./assets/art/svg/human/standing-19.svg";

const questions = {
  age: {
    Q: "What's your age group?",
    A: [
      { id: "1", label: "over 65" },
      { id: "2", label: "less than 65" }
    ]
  }
};

export default class Para extends React.Component {
  constructor(props) {
    super(props);
    this.scroll = this.scroll.bind(this);
  }

  scroll = (pageNumber) => {
    this.parallax.scrollTo(pageNumber);
  };
  componentDidMount() {
    this.props.onRef && this.props.onRef(this);
  }
  render() {
    const classes = makeStyles((theme) => ({
      human: {
        height: "50vh",
        maxWidth: "100ch",
        maxHeight: "100ch"
      }
    }));
    const cloudRightLeftEnd = "80%";
    const cloudLeftLeftEnd =
      Number((document.body.clientWidth > 500 ? 0.15 : -0.05) * 100).toFixed(
        1
      ) + "%";

    return (
      <Parallax ref={(ref) => (this.parallax = ref)} pages={3}>
        <ParallaxLayer
          offset={0}
          speed={1}
          style={{
            backgroundImage:
              "linear-gradient(-180deg, #5fb6d9 0%, #ededdf 100%)"
          }}
        />
        <ParallaxLayer
          offset={1}
          speed={1}
          style={{
            backgroundImage:
              "linear-gradient(-180deg, #ededdf 0%, #fde3ff 100%)"
          }}
        />
        <ParallaxLayer
          offset={1.999999}
          speed={1}
          style={{
            backgroundImage: "linear-gradient(-180deg, #fff  0%, #fff 100%)"
          }}
        />
        <ParallaxLayer offset={0} speed={1.2}>
          <Cloud width="15%" left={cloudLeftLeftEnd} opacity="0.5" />
        </ParallaxLayer>
        <ParallaxLayer offset={0.1} speed={1.2}>
          <Cloud width="10%" left={cloudRightLeftEnd} opacity="0.5" />
        </ParallaxLayer>

        <ParallaxLayer offset={1} speed={-0.2}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            viewBox="0 0 1080 500"
          >
            <circle cx="340" cy="100" r="40" fill="#d49c19" />
          </svg>
        </ParallaxLayer>
        <ParallaxLayer offset={1.5} speed={-2.8}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            version="1.1"
            viewBox="0 0 1920 1080"
          >
            <circle cx="300" cy="480" r="400" fill="#729466" />
          </svg>
        </ParallaxLayer>
        <ParallaxLayer offset={0.2} speed={-2.3}>
          <Hill height={window.screen.height} />
        </ParallaxLayer>
        <ParallaxLayer offset={0.45} speed={-2.5}>
          <BuildingBkg height={window.screen.height} />
        </ParallaxLayer>
        <ParallaxLayer offset={0.55} speed={-2.7}>
          <Building height={window.screen.height} />
        </ParallaxLayer>

        <ParallaxLayer offset={1} speed={1}>
          <SurveyGrid
            Q={questions["age"]["Q"]}
            A={questions["age"]["A"]}
            scroll={() => {
              this.scroll(2);
            }}
          >
            <Standing19 className={classes.human} />
          </SurveyGrid>
        </ParallaxLayer>
        <ParallaxLayer
          offset={0.15}
          speed={0.3}
          onClick={() => this.scroll(1)}
          style={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
          }}
        >
          <LogoGroup />
          
        </ParallaxLayer>
        <ParallaxLayer
          offset={0.15}
          speed={0.3}
        >
          <Title />
          
        </ParallaxLayer>
      </Parallax>
    );
  }
}
